import styles from './Tour.module.scss';
import { Container, Col, Row, Image } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faClapperboard, faCamera } from '@fortawesome/free-solid-svg-icons';


const Tour = () => {
    return (
        <Container className={styles["container"]}>
            <Col className={styles["header"]}>
                <h2>Wirtualny spacer / Fotografia panormaiczna 360</h2>
            </Col>
            <Col className={styles["tour"]}>
                <iframe src="https://sb360.online/i5uvyf" height="450" width="650" allow="microphone;fullscreen" allowfullscreen allowmicrophone ></iframe>
            </Col>
            <Col>
                <p>
                    Prezentujemy Pastwu nasz nowoczesn usug wirtualnego spaceru i fotografii 360.  W odrnieniu od zwykego zdjcia pozwala spojrze na to samo wntrze z innej perspektywy, dziki czemu pozwoli uczyni prezentacj Pastwa nieruchomoci jeszcze bardziej atrakcyjn i dostpn dla potencjalnych nabywcw.
                </p>
            </Col>
            <Row>
                <Col className={styles["about-container"]}>
                <div className={styles["icon-container"]}>
                <FontAwesomeIcon icon={faCamera} />
                </div>
                <div className={styles["description"]}>
                    <h3>Fotografia 360</h3>
                    <p>
                    Dziki uyciu nowoczesnego sprztu do fotografii sferycznych nasze wysokiej jakoci fotografie 360 stopni pozwalaj uwieczni cae otoczenie nieruchomoci, zarwno wewntrz, jak i na zewntrz. Dziki nim potencjalni nabywcy mog zanurzy si w atmosfer nieruchomoci, obserwujc j z rnych perspektyw.
                    </p>
                </div>
                </Col>
                <Col className={styles["about-container"]}>
                <div className={styles["icon-container"]}>
                <FontAwesomeIcon icon={faClapperboard} />
                </div>
                <div className={styles["description"]}>
                    <h3>Wirtualny Spacer</h3>
                    <p>
                    Zapewniamy moliwo przegldania nieruchomoci online, dziki wirtualnemu spacerowi. Klienci mog komfortowo przechadza si po nieruchomoci, niemal jakby byli na miejscu. To rozwizanie sprawia, e potencjalni nabywcy mog zobaczy kady detal nieruchomoci, bez koniecznoci obecnoci na miejscu.
                    </p>
                </div>
                </Col>
            </Row>       
        </Container>
    )
}

export default Tour;