import { useNavigate  } from "react-router-dom";
import { useState } from "react";

import { Container, Row, Col, Accordion, Image } from "react-bootstrap";
import styles from './Description.module.scss';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faHouseChimney, faKey, faCircleInfo } from '@fortawesome/free-solid-svg-icons';
import DescriptionList from "../../features/DescriptionList/DescriptionList";
import { faShield, faCameraRetro, faGraduationCap, faComments, faListCheck, faBusinessTime } from '@fortawesome/free-solid-svg-icons';


const Description = () => {
  const navigate = useNavigate();
  const [isActive, setIsActive] = useState(false);

  const descritpon = [
    {
      name: "KOMPETENCJE",
      description: "Posiadamy wyksztacenie w dziedzinie geodezji oraz finansw. W tym roku zostaa nam rwnie przyznana licencja porednikw nieruchomoci, poprzedzona zdanym egzaminem zawodowym.",
      icon: faGraduationCap
    },
    {
      name: "DOWIADCZENIE",
      description: "Jestemy praktykami. Od wielu lat dziaamy w brany geodezyjnej oraz rachunkowej. Na bieco aktualizujemy wiedz z zakresu obowizujcych przepisw prawa.",
      icon: faBusinessTime
    
    },
    {
      name: "KOMPLEKSOWA OBSUGA",
      description: "Z uwagi na wieloletnie dowiadczenie, jestemy w stanie przeprowadzi Pastwa przez cay proces inwestycyjny, doradzajc przy tym najbardziej optymalne rozwizanie.",
      icon: faComments
    },
    {
      name: "BEZPIECZESTWO",
      description: "Wybierajc nasze biuro, moecie by Pastwo spa spokojnie. Jestemy w stanie poprowadzi Pastwa sprawy ograniczajc ryzyko do minimum. W trosce o Pastwa dobro, posiadamy rwnie polis ubezpieczeniow, ktra ma w jak najlepszy sposb zabezpieczy Pastwa mienie oraz interesy.",
      icon: faShield
    },
    {
      name: "PROFESJONALIZM",
      description: "Kadego klienta traktujemy w sposb indywidualny. Mamy wiadomo, e Pastwa sytuacje bywaj bardzo rne, dlatego staramy si dokada wszelkich stara by zagwarantowa pen satysfakcj, bez wzgldu na okolicznoci.",
      icon: faListCheck
    },
    {
      name: "NOWOCZESNY SPRZT",
      description: "Na wyposaeniu biura posiadamy sprzt geodezyjny, ktrym jestemy w stanie w Pastwa obecnoci wskaza granice dziaek. Dysponujemy take dronem, oraz kamer sferyczn, ktra umoliwia przygotowanie wirtualnych spacerw.",
      icon: faCameraRetro
    }
  ]

  return (
    <Container className={styles["container"]}>
      <h2>MW - NIERUCHOMOCI</h2>
      <div className={styles["description-container"]}>
        <div className={styles["photo"]}>
          <Image src={process.env.PUBLIC_URL + '/logo_company.svg'} fluid />
        </div>
        <div className={styles["description"]}>
          <Accordion defaultActiveKey={['0']} alwaysOpen>
            {descritpon.map(desc => <DescriptionList name={desc.name} icon={desc.icon } description={desc.description} />)}
          </Accordion>
        </div>
      </div>
      <Row className={styles["icon-container"]}>
        <Col className={styles["icon"]} onClick={() => navigate('/sell')} xs={12} md={4}>
          <FontAwesomeIcon icon={faHouseChimney} />
          <div className={styles["description"]}>
            <h3>Sprzedaj</h3>
            <div className={styles["text-container"]}>
              <p>Kompleksowa obsuga caego procesu sprzeday. Wirtualny spacer, fotografia dronem, profesjonalny marketing w social media.</p>
            </div>
          </div>
        </Col>
        <Col className={styles["icon"]} onClick={() => navigate('/buy')} xs={12} md={4}>
          <FontAwesomeIcon icon={faKey} />
          <div className={styles["description"]}>
            <h3>Kup</h3>
            <div className={styles["text-container"]}>
            <p>Pomoemy Ci wybra najlepsze rozwizanie dla Twojej sytuacji. Nie wiesz na co zwraca uwag? Zabezpieczymy Twoj transakcj zakupu w taki sposb, eby mg by pewien, e Twoje interesy s bezpieczne.</p>
            </div>

          </div>
        </Col>
        <Col className={styles["icon"]} onClick={() => navigate('/about')} xs={12} md={4}>
          <FontAwesomeIcon icon={faCircleInfo} />
          <div className={styles["description"]}>
            <h3>O nas</h3>
            <div className={styles["text-container"]}>
            <p>To dla Ciebie za mao? Sprawd stron o nas i dowiedz si wicej.</p>
            </div>

          </div>
        </Col>
      </Row>
    </Container>
  )
}

export default Description;