import { Container, Row, Col } from "react-bootstrap";
import styles from './AboutUs.module.scss';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faBriefcase, faHandshake, faMapLocationDot } from '@fortawesome/free-solid-svg-icons';


const AboutUs = () => {
  return (
    <Container className={styles["container"]}>
      <h2>Dlaczego my?</h2>
      <Row>
        <Col className={styles["about-container"]}>
        <div className={styles["icon-container"]}>
          <FontAwesomeIcon icon={faBriefcase} />
        </div>
          <div className={styles["description"]}>
            <h3>Formalnoci</h3>
            <p>Wizyty w urzdach, sdach oraz innych instytucjach zwizanych z obsug nieruchomoci, z pewnoci mog przysporzy wiele stresu. Dlatego wszystkie formalnoci spenimy za Ciebie.</p>
          </div>
        </Col>
        <Col className={styles["about-container"]}>
        <div className={styles["icon-container"]}>
          <FontAwesomeIcon icon={faHandshake} />
        </div>
          <div className={styles["description"]}>
            <h3>Indywidualna Obsga Klienta</h3>
            <p>Wiemy jak wana jest dla Pastwa dyspozycyjno, dlatego telefon odbieramy nawet poza godzinami pracy biura. Jestemy w stanie dostosowa si do waszego planu dnia.</p>
          </div>
        </Col>
        <Col className={styles["about-container"]}>
          <div className={styles["icon-container"]}>
            <FontAwesomeIcon icon={faMapLocationDot} />
          </div>
          <div className={styles["description"]}>
            <h3>Wszechstronno</h3>
            <p>Z uwagi na nasze dowiadczenie zawodowe jestemy na bieco z przepisami prawa. Posiadamy nowoczesny sprzt, dziki ktremu moemy wiadczy nowoczesn usug. ledzimy rwnie najnowsze trendy w social mediach.</p>
          </div>
        </Col>
      </Row>
    </Container>
  )
}

export default AboutUs;