import styles from './About.module.scss';
import { Container, Row, Col } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faGraduationCap, faHouseChimney, faUmbrella, faBusinessTime } from '@fortawesome/free-solid-svg-icons';
import Maps from '../../features/Map/Map';

const About = () => {
  return (
    <Container>
      <Col className={styles["header"]}>
        <h2>O nas</h2>
      </Col>
      <Row>
        <Col className={styles["details-container"]} lg={6}>
          <div className={styles["icon"]}><FontAwesomeIcon icon={faGraduationCap} /></div>
          <div>
            <h3>Licencjonowani Porednicy</h3>
            <p>
            Posiadamy licencje porednikw w obrocie nieruchomociami numer, numer. Codziennie aktualizujemy wiedz nie tylko z zakresu obrotu nieruchomoci, ale rwnie z przepisw szeroko pojtego prawa budowlanego oraz podatkowego. W ten sposb moemy speni oczekiwania nawet najbardziej wymagajcego klienta.
            </p>
          </div>
        </Col>
        <Col className={styles["details-container"]} lg={6}>
          <div className={styles["icon"]}><FontAwesomeIcon icon={faHouseChimney} /></div>
          <div>
            <h3>Kompleksowa pomoc w zakupie nieruchomoi</h3>
            <p>
              Nasze biuro oferuje kompleksow pomoc poczynajc od od gruntownej analizy lokalizacji, uwzgldniajc zarwno aspekty rynkowe, jak i indywidualne preferencje klienta.
              Sprawdzimy wybran dziak rwnie pod ktem prawnym, a take geodezyjnym. Ograniczymy do minimum udzia w kompletowaniu dokumentacji oraz formalnociach.
            </p>
          </div>
        </Col>
        <Col className={styles["details-container"]} lg={6}>
          <div className={styles["icon"]}><FontAwesomeIcon icon={faUmbrella} /></div>
          <div>
            <h3>Bezpieczestwo</h3>
            <p>
              Rozumiemy, e bezpieczestwo w przeprowadzaniu transakcji powinno by priorytetem. Kadego klienta traktujemy indywidualnie, zapewniajc wsparcie na kadym etapie, tak, eby we wszystkich etapach procesu inwestycyjnego mg spa spokojnie. Dodatkowo, wszystkie prowadzone przez nas transakcje s objte ubezpieczeniem.
            </p>
          </div>
        </Col>
        <Col className={styles["details-container"]} lg={6}>
          <div className={styles["icon"]}><FontAwesomeIcon icon={faBusinessTime} /></div>
          <div>
            <h3>Oszczdno czasu</h3>
            <p>
            W dzisiejszym wiecie czas jest droszy od pienidzy. Staramy si ograniczy Pastwa udzia do minimum, maksymalizujc przy tym bezpieczestwo. Z nami zapomn Pastwo o wizytach w urzdach, sdach oraz innych instytucjach, w ktrych wizyta jest niezbdna w procesie obrotu nieruchomociami.
            </p>
          </div>
        </Col>
      </Row>
      <Maps />
    </Container>
  )
}

export default About;